.edit-form {

    width: 100%; height: 100%;
    position: fixed;
    background-color: rgba(39, 39, 39, 0.829);
    padding: 2rem;

}
.edit-form button {
    padding: 0.5rem 1rem;
}
.edit-form div {
    display: flex;
    
}
.edit-form div div {
    display: flex; flex-direction: column;
    width: 40%;
    
}
.edit-form div div input {
    margin-bottom: 1rem;
    font-size: 20px;
    
}