.dashboard-admin {
    width: 100%; height: 100%;
}

.dashboard-header {
    height: 10%;
    display: flex;
    align-items: center; 
    padding: 0 1rem;
    background-color: rgb(127, 212, 194);
}
.dashboard-header h1 {
    font-size: 18px;
    font-weight: 400;
}

.dashboard-container {
    height: 90%;
    display: flex;
}
.dashboard-container .dashboard-sidebar {
    width: 20%; 
    padding: 1rem;
    display: flex; flex-direction: column;
    align-items: center;
    background-color: rgb(54, 54, 54);
}
.dashboard-container .dashboard-sidebar li {
    list-style: none;
}
.dashboard-container .dashboard-sidebar li a {
    font-size: 18px;
    font-weight: 200;

    text-decoration: none;
    color: white;
    cursor: pointer;
}
.dashboard-container .dashboard-sidebar li a.active {
    color: rgb(131, 199, 171);
}

.dashboard-container .dashboard-panel {
    
    overflow-x: inherit;
    width: 80%;
    background-color:rgb(184, 184, 184) ;
    
}
.dashboard-container .dashboard-panel .panel-not {
    width: 33%; height: 33%;
    background-color: rgb(205, 212, 211);
    position: absolute;
    left: 40%; top: 30%;
    display: flex; flex-direction: column;
    align-items: center; justify-content: center;
}
.dashboard-container .dashboard-panel .panel-not p {
    font-size: 18px;
}
.dashboard-container .dashboard-panel .panel-not div button {
    padding: 0.5rem;
    margin: 0.5rem;
}
.dashboard-container .dashboard-panel .agend-panel {
    height: 100%;
}
.dashboard-container .dashboard-panel .agend-panel .item-action-notification {
    background-color: rgb(5, 5, 5);
    color: cornsilk;
    padding: 0.5rem 1rem;
}
.dashboard-container .dashboard-panel .agend-panel .agend-header {
    width: 100%;
    background-color: slategrey;
    
    
}
.dashboard-container .dashboard-panel .agend-panel .agend-header h2 {
    font-size: 18px;
    font-weight: 200;
    color: white;
    padding: 1rem; margin: 0;
}

.dashboard-container .dashboard-panel .agend-panel .agend-list {
    height: 90%;
    display: flex; flex-wrap: wrap;
    overflow-y: scroll;
  
}
.dashboard-container .dashboard-panel .agend-panel .agend-list .agend-item {
    width: 20%;
    height: 18rem;
    
    background-color: white;
    padding: 0.5rem 1rem;
    margin-bottom: 1rem;
    border-radius: 0.2rem;
    margin-right: 1rem;
}
.dashboard-container .dashboard-panel .agend-panel .agend-list .agend-item .item-header {
    padding-bottom: 1rem;
}
.dashboard-container .dashboard-panel .agend-panel .agend-list .agend-item .item-header .header-icons {
    display: flex;
}
.dashboard-container .dashboard-panel .agend-panel .agend-list .agend-item .item-header .header-icons .icon {
    font-size: 24px;
    margin: 0.5rem;
    cursor: pointer;
    color: rgb(95, 95, 95);
    
}
.dashboard-container .dashboard-panel .agend-panel .agend-list .agend-item .item-header .header-icons .icon:hover {
    color: rgb(138, 202, 173);
}
.dashboard-container .dashboard-panel .agend-panel .agend-list .agend-item .item-header p {
    
    font-size: 14px;
    font-weight: 400;
}
.dashboard-container .dashboard-panel .agend-panel .agend-list .agend-item .item-body h2 {
    font-size: 18px;
    font-weight: 600;
}
.dashboard-container .dashboard-panel .agend-panel .agend-list .agend-item .item-body p {
    font-size: 14px;
}


